export default function (props, ref, key) {
    return (
        <skoash.Screen
            {...props}
            ref={ref}
            key={key}
            id="flip"
        >
                <skoash.Component className="header">
                    <span>
                        HERE'S YOUR FLIP!
                    </span>
                </skoash.Component>
        </skoash.Screen>
    );
}



